@charset "UTF-8";
/*左边工具栏以及编辑节点的样式*/
/**
 * outline styles
 */
.djs-outline {
  fill: none;
  visibility: hidden;
}
.djs-element.hover .djs-outline,
.djs-element.selected .djs-outline {
  visibility: visible;
  shape-rendering: crispEdges;
  stroke-dasharray: 3,3;
}
.djs-element.selected .djs-outline {
  stroke: #8888FF;
  stroke-width: 1px;
}
.djs-element.hover .djs-outline {
  stroke: #FF8888;
  stroke-width: 1px;
}
.djs-shape.connect-ok .djs-visual > :nth-child(1) {
  fill: #DCFECC /* light-green */ !important;
}
.djs-shape.connect-not-ok .djs-visual > :nth-child(1),
.djs-shape.drop-not-ok .djs-visual > :nth-child(1) {
  fill: #f9dee5 /* light-red */ !important;
}
.djs-shape.new-parent .djs-visual > :nth-child(1) {
  fill: #F7F9FF !important;
}
svg.drop-not-ok {
  background: #f9dee5 /* light-red */ !important;
}
svg.new-parent {
  background: #F7F9FF /* light-blue */ !important;
}
.djs-connection.connect-ok .djs-visual > :nth-child(1),
.djs-connection.drop-ok .djs-visual > :nth-child(1) {
  stroke: #90DD5F /* light-green */ !important;
}
.djs-connection.connect-not-ok .djs-visual > :nth-child(1),
.djs-connection.drop-not-ok .djs-visual > :nth-child(1) {
  stroke: #E56283 /* light-red */ !important;
}
.drop-not-ok,
.connect-not-ok {
  cursor: not-allowed;
}
.djs-element.attach-ok .djs-visual > :nth-child(1) {
  stroke-width: 5px !important;
  stroke: rgba(255, 116, 0, 0.7) !important;
}
/**
* Selection box style
*
*/
.djs-lasso-overlay {
  fill: rgb(255, 116, 0);
  fill-opacity: 0.1;

  stroke-dasharray: 5 1 3 1;
  stroke: rgb(255, 116, 0);

  shape-rendering: crispEdges;
  pointer-events: none;
}
/**
 * Resize styles
 */
.djs-resize-overlay {
  fill: none;

  stroke-dasharray: 5 1 3 1;
  stroke: rgb(255, 116, 0);

  pointer-events: none;
}
.djs-resizer-hit {
  fill: none;
  pointer-events: all;
}
.djs-resizer-visual {
  fill: white;
  stroke-width: 1px;
  stroke: black;
  shape-rendering: crispEdges;
  stroke-opacity: 0.2;
}
.djs-cursor-resize-nwse,
.djs-resizer-nw,
.djs-resizer-se {
  cursor: nwse-resize;
}
.djs-cursor-resize-nesw,
.djs-resizer-ne,
.djs-resizer-sw {
  cursor: nesw-resize;
}
.djs-shape.djs-resizing > .djs-outline {
  visibility: hidden !important;
}
.djs-shape.djs-resizing > .djs-resizer {
  visibility: hidden;
}
.djs-dragger > .djs-resizer {
  visibility: hidden;
}
/**
 * drag styles
 */
.djs-dragger .djs-visual circle,
.djs-dragger .djs-visual path,
.djs-dragger .djs-visual polygon,
.djs-dragger .djs-visual polyline,
.djs-dragger .djs-visual rect,
.djs-dragger .djs-visual text {
  fill: none !important;
  stroke: rgb(255, 116, 0) !important;
}
.djs-dragging {
  opacity: 0.3;
}
.djs-dragging,
.djs-dragging > * {
  pointer-events: none !important;
}
.djs-dragging .djs-context-pad,
.djs-dragging .djs-outline {
  display: none !important;
}
/**
 * no pointer events for visual
 */
.djs-visual,
.djs-outline {
  pointer-events: none;
}
/**
 * all pointer events for hit shape
 */
.djs-shape .djs-hit {
  pointer-events: all;
}
.djs-connection .djs-hit {
  pointer-events: stroke;
}
/**
 * shape / connection basic styles
 */
.djs-connection .djs-visual {
  stroke-width: 2px;
  fill: none;
}
.djs-cursor-grab {
  cursor: -webkit-grab;
  cursor: grab;
}
.djs-cursor-grabbing {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.djs-cursor-crosshair {
  cursor: crosshair;
}
.djs-cursor-move {
  cursor: move;
}
.djs-cursor-resize-ns {
  cursor: ns-resize;
}
.djs-cursor-resize-ew {
  cursor: ew-resize;
}
/**
 * snapping
 */
.djs-snap-line {
  stroke: rgb(255, 195, 66);
  stroke: rgba(255, 195, 66, 0.50);
  stroke-linecap: round;
  stroke-width: 2px;
  pointer-events: none;
}
/**
 * snapping
 */
.djs-crosshair {
  stroke: #555;
  stroke-linecap: round;
  stroke-width: 1px;
  pointer-events: none;
  shape-rendering: crispEdges;
  stroke-dasharray: 5, 5;
}
/**
 * palette
 */
.djs-palette {
  position: absolute;
  left: 20px;
  top: 20px;

  -webkit-box-sizing: border-box;

          box-sizing: border-box;
  width: 48px;
}
.djs-palette .separator {
  margin: 0 5px;
  padding-top: 5px;

  border: none;
  border-bottom: solid 1px #DDD;

  clear: both;
}
.djs-palette .entry:before {
  vertical-align: middle;
}
.djs-palette .djs-palette-toggle {
  cursor: pointer;
}
.djs-palette .entry,
.djs-palette .djs-palette-toggle {
  color: #333;
  font-size: 30px;

  text-align: center;
}
.djs-palette .entry {
  float: left;
}
.djs-palette .entry img {
  max-width: 100%;
}
.djs-palette .djs-palette-entries:after {
  content: '';
  display: table;
  clear: both;
}
.djs-palette .djs-palette-toggle:hover {
  background: #666;
}
.djs-palette .entry:hover {
  color: rgb(255, 116, 0);
}
.djs-palette .highlighted-entry {
  color: rgb(255, 116, 0) !important;
}
.djs-palette .entry,
.djs-palette .djs-palette-toggle {
  width: 46px;
  height: 46px;
  line-height: 46px;
  cursor: default;
}
/**
 * Palette open / two-column layout is controlled via
 * classes on the palette. Events to hook into palette
 * changed life-cycle are available in addition.
 */
.djs-palette.two-column.open {
  width: 94px;
}
.djs-palette:not(.open) .djs-palette-entries {
  display: none;
}
.djs-palette:not(.open) {
  overflow: hidden;
}
.djs-palette.open .djs-palette-toggle {
  display: none;
}
/**
 * context-pad
 */
.djs-overlay-context-pad {
  width: 72px;
}
.djs-context-pad {
  position: absolute;
  display: none;
  pointer-events: none;
}
.djs-context-pad .entry {
  width: 22px;
  height: 22px;
  text-align: center;
  display: inline-block;
  font-size: 22px;
  margin: 0 2px 2px 0;

  border-radius: 3px;

  cursor: default;

  background-color: #FEFEFE;
  -webkit-box-shadow: 0 0 2px 1px #FEFEFE;
          box-shadow: 0 0 2px 1px #FEFEFE;
  pointer-events: all;
}
.djs-context-pad .entry:before {
  vertical-align: top;
}
.djs-context-pad .entry:hover {
  background: rgb(255, 252, 176);
}
.djs-context-pad.open {
  display: block;
}
/**
 * popup styles
 */
.djs-popup .entry {
  line-height: 20px;
  white-space: nowrap;
  cursor: default;
}
/* larger font for prefixed icons */
.djs-popup .entry:before {
  vertical-align: middle;
  font-size: 20px;
}
.djs-popup .entry > span {
  vertical-align: middle;
  font-size: 14px;
}
.djs-popup .entry:hover,
.djs-popup .entry.active:hover {
  background: rgb(255, 252, 176);
}
.djs-popup .entry.disabled {
  background: inherit;
}
.djs-popup .djs-popup-header .entry {
  display: inline-block;
  padding: 2px 3px 2px 3px;

  border: solid 1px transparent;
  border-radius: 3px;
}
.djs-popup .djs-popup-header .entry.active {
  color: rgb(255, 116, 0);
  border: solid 1px rgb(255, 116, 0);
  background-color: #F6F6F6;
}
.djs-popup-body .entry {
  padding: 4px 10px 4px 5px;
}
.djs-popup-body .entry > span {
  margin-left: 5px;
}
.djs-popup-body {
  background-color: #FEFEFE;
}
.djs-popup-header {
  border-bottom: 1px solid #DDD;
}
.djs-popup-header .entry {
  margin: 1px;
  margin-left: 3px;
}
.djs-popup-header .entry:last-child {
  margin-right: 3px;
}
/**
 * popup / palette styles
 */
.djs-popup, .djs-palette {
  background: #FAFAFA;
  border: solid 1px #CCC;
  border-radius: 2px;
}
/**
 * touch
 */
.djs-shape,
.djs-connection {
  -ms-touch-action: none;
      touch-action: none;
}
.djs-segment-dragger,
.djs-bendpoint {
  display: none;
}
/**
 * bendpoints
 */
.djs-segment-dragger .djs-visual {
  fill: rgba(255, 255, 121, 0.2);
  stroke-width: 1px;
  stroke-opacity: 1;
  stroke: rgba(255, 255, 121, 0.3);
}
.djs-bendpoint .djs-visual {
  fill: rgba(255, 255, 121, 0.8);
  stroke-width: 1px;
  stroke-opacity: 0.5;
  stroke: black;
}
.djs-segment-dragger:hover,
.djs-bendpoints.hover .djs-segment-dragger,
.djs-bendpoints.selected .djs-segment-dragger,
.djs-bendpoint:hover,
.djs-bendpoints.hover .djs-bendpoint,
.djs-bendpoints.selected .djs-bendpoint {
  display: block;
}
.djs-drag-active .djs-bendpoints * {
  display: none;
}
.djs-bendpoints:not(.hover) .floating {
  display: none;
}
.djs-segment-dragger:hover .djs-visual,
.djs-segment-dragger.djs-dragging .djs-visual,
.djs-bendpoint:hover .djs-visual,
.djs-bendpoint.floating .djs-visual {
  fill: yellow;
  stroke-opacity: 0.5;
  stroke: black;
}
.djs-bendpoint.floating .djs-hit {
  pointer-events: none;
}
.djs-segment-dragger .djs-hit,
.djs-bendpoint .djs-hit {
  pointer-events: all;
  fill: none;
}
.djs-segment-dragger.horizontal .djs-hit {
  cursor: ns-resize;
}
.djs-segment-dragger.vertical .djs-hit {
  cursor: ew-resize;
}
.djs-segment-dragger.djs-dragging .djs-hit {
