/*左边工具栏以及编辑节点的样式*/
/**
 * outline styles
 */
.djs-outline {
  fill: none;
  visibility: hidden;
}
.djs-element.hover .djs-outline,
.djs-element.selected .djs-outline {
  visibility: visible;
  shape-rendering: crispEdges;
  stroke-dasharray: 3,3;
}
.djs-element.selected .djs-outline {
  stroke: #8888FF;
  stroke-width: 1px;
}
.djs-element.hover .djs-outline {
  stroke: #FF8888;
  stroke-width: 1px;
}
.djs-shape.connect-ok .djs-visual > :nth-child(1) {
  fill: #DCFECC /* light-green */ !important;
}
.djs-shape.connect-not-ok .djs-visual > :nth-child(1),
.djs-shape.drop-not-ok .djs-visual > :nth-child(1) {
  fill: #f9dee5 /* light-red */ !important;
}
.djs-shape.new-parent .djs-visual > :nth-child(1) {
  fill: #F7F9FF !important;
}
svg.drop-not-ok {
  background: #f9dee5 /* light-red */ !important;
}
svg.new-parent {
  background: #F7F9FF /* light-blue */ !important;
}
.djs-connection.connect-ok .djs-visual > :nth-child(1),
.djs-connection.drop-ok .djs-visual > :nth-child(1) {
  stroke: #90DD5F /* light-green */ !important;
}
.djs-connection.connect-not-ok .djs-visual > :nth-child(1),
.djs-connection.drop-not-ok .djs-visual > :nth-child(1) {
  stroke: #E56283 /* light-red */ !important;
}
.drop-not-ok,
.connect-not-ok {
  cursor: not-allowed;
}
.djs-element.attach-ok .djs-visual > :nth-child(1) {
  stroke-width: 5px !important;
  stroke: rgba(255, 116, 0, 0.7) !important;
}
/**
* Selection box style
*
*/
.djs-lasso-overlay {
  fill: rgb(255, 116, 0);
  fill-opacity: 0.1;

  stroke-dasharray: 5 1 3 1;
  stroke: rgb(255, 116, 0);

  shape-rendering: crispEdges;
  pointer-events: none;
}
/**
 * Resize styles
 */
.djs-resize-overlay {
  fill: none;

  stroke-dasharray: 5 1 3 1;
  stroke: rgb(255, 116, 0);

  pointer-events: none;
}
.djs-resizer-hit {
  fill: none;
  pointer-events: all;
}
.djs-resizer-visual {
  fill: white;
  stroke-width: 1px;
  stroke: black;
  shape-rendering: crispEdges;
  stroke-opacity: 0.2;
}
.djs-cursor-resize-nwse,
.djs-resizer-nw,
.djs-resizer-se {
  cursor: nwse-resize;
}
.djs-cursor-resize-nesw,
.djs-resizer-ne,
.djs-resizer-sw {
  cursor: nesw-resize;
}
.djs-shape.djs-resizing > .djs-outline {
  visibility: hidden !important;
}
.djs-shape.djs-resizing > .djs-resizer {
  visibility: hidden;
}
.djs-dragger > .djs-resizer {
  visibility: hidden;
}
/**
